/* Estilos para tablets */
@media only screen and (min-width: 768px) and (max-width: 1024px) {

  body {
    overflow: hidden; /* Evitar el desplazamiento vertical */
  }

  .home-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    flex-direction: column;
    position: relative;
    background: linear-gradient(to bottom, transparent 40%, #007BFF 100%);
  }

  .logo-container {
    position: fixed;
    margin-top: -580px;
  }

  .logo {
    max-width: 220px; /* Tamaño del logo para móvil */
    height: auto;
    cursor: pointer;
    z-index: 10;
  }

  .circle-images-container {
    position: absolute;
    top: 20%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    opacity: 0;
    pointer-events: none;
    transition: all 0.5s ease;
  }

  .circle-images-container.show {
    opacity: 1;
    left: 85%; /* Mover un poco más a la derecha en móvil */
    pointer-events: auto;
  }

  /* Estilos base de las imágenes circulares */
  .circle-image {
    border-radius: 50%; /* Hacer que las imágenes sean circulares */
    margin-bottom: 15px; /* Espacio entre las imágenes */
    cursor: pointer;
    transition: transform 0.3s ease;
  }

  
/* Redes sociales debajo del logo */
.social-media-container {
  display: flex;
  justify-content: center;
  margin-top: -100px; /* Espacio entre el logo y las redes sociales */
  gap: 10px; /* Separación entre los iconos de las redes sociales */
}

/* Estilos individuales para cada ícono de red social */
.facebook-icon {
  width: 50px;
  height: 50px;
}

.whatsapp-icon {
  width: 45px;
  height: 45px;
}

.instagram-icon {
  width: 55px;
  height: 55px;
}

.linkedin-icon {
  width: 50px;
  height: 50px;
}

.github-icon {
  width: 50px;
  height: 50px;
}

.cv-icon {
  width: 45px;
  height: 45px;
}

.social-media-icon {
  transition: transform 0.3s ease;
  border-radius: 50%; /* Convertir las imágenes en círculos */
}

.social-media-icon:hover {
  transform: scale(1.2); /* Efecto hover para aumentar ligeramente el tamaño */
}

.proyectos{
  color: white;
  text-size-adjust: 10px;
  margin-top: 70px;
}


  /* SLIDER FIJO (sin desplazamiento) */
  .slider-container {
    position: fixed; /* Fijar en la pantalla */
    bottom: 100px; /* Subir el slider más arriba ajustando la separación inferior */
    left: 50%;
    transform: translateX(-50%); /* Centrar horizontalmente */
    width: 90%; /* Ancho del slider */
    overflow-x: auto; /* Permitir desplazamiento horizontal en móviles */
    white-space: nowrap; /* Evitar que los proyectos se salten a una nueva línea */
    display: flex; /* Asegurar que las imágenes estén alineadas en fila */
    justify-content: flex-start; /* Alinear las imágenes horizontalmente */
    gap: 20px; /* Espacio entre las imágenes */
    scroll-behavior: smooth; /* Añadir un desplazamiento suave */
  }

  /* Estructura del slider */
  .slider {
    display: flex; /* Alinear los proyectos horizontalmente */
    flex-direction: row; /* Garantizar que los proyectos estén uno al lado del otro */
    width: auto; /* Permitir que el ancho sea dinámico según el contenido */
  }

  /* Estilos de cada slide */
  .slide {
    min-width: 250px; /* Reducir un poco el tamaño del slide en móviles */
    max-width: 250px; /* Mantener todos los proyectos con el mismo ancho */
    height: 200px; /* Reducir la altura para dispositivos móviles */
    margin: 0 20px;
    position: relative;
    border: 5px solid white; /* Borde blanco más grueso */
    border-radius: 10px;
  }

  /* Imagen del proyecto con tamaño uniforme */
  .project-image {
    width: 100%;
    height: 100%; /* Asegurarse de que todas las imágenes llenen el slide */
    border-radius: 1px;
    object-fit: cover; /* Ajustar la imagen sin distorsionarla */
  }

  /* Posición de los iconos de tecnologías en la esquina inferior derecha */
  .technologies {
    position: absolute;
    bottom: 10px;
    right: 10px;
    display: flex; /* Alinear los iconos de tecnologías horizontalmente */
    gap: 5px;
  }

  /* Tamaño de los iconos de tecnologías */
  .technology-icon {
    width: 30px;
    height: 30px;
    background-color: #828991;
  }
}
