@media only screen and (min-width: 1200px) {
    html, body {
        margin: 0;
        padding: 0;
        overflow-y: hidden;
        height: 100%;
    }

    * {
        box-sizing: border-box;
    }

    .ecoruta-container {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100vh;
        flex-direction: column;
        position: relative;
        background: linear-gradient(to bottom, transparent 60%, #007BFF 100%);
        text-align: center; /* Centra todo el contenido del contenedor */
    }

    .ecoruta-container h1 {
        color: #007BFF;
    }

    .ecoruta-container p {
        font-size: 18px;
        color: #ffffff;
        text-align: center; /* Centra el texto del párrafo */
        line-height: 1.6; /* Aumenta el espacio entre las líneas */
        max-width: 100%; /* Limita el ancho del texto para mayor legibilidad */
    }

    .ecoruta-container .btn {
        background-color: #007BFF;
        color: white;
        padding: 10px 20px;
        text-decoration: none;
        border-radius: 5px;
        margin-top: 30px;
    }

    .ecoruta-container .btn:hover {
        background-color: #0056b3;
    }

    /* Contenedor del celular */
    .phone-frame {
        position: relative;
        width: 600px;
        height: 600px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    /* Carrusel detrás de la imagen del celular */
    .carousel-container {
        position: absolute;
        width: 230px;
        height: 510px;
        top: 7.5%;
        left: 50%;
        transform: translateX(-50%);
        z-index: 1;
        overflow: hidden;
    }

    .carousel-slide img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 10px;
    }

    /* La imagen del celular, colocada por encima */
    .phone-image {
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: 2;
        pointer-events: none;
    }

    .carousel-control {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        background-color: rgba(0, 0, 0, 0.5);
        color: white;
        border: none;
        font-size: 24px;
        cursor: pointer;
        padding: 10px;
        z-index: 100;
        user-select: none;
    }

    .carousel-control.prev {
        left: 10px;
    }

    .carousel-control.next {
        right: 10px;
    }

    .carousel-control:hover {
        background-color: rgba(0, 0, 0, 0.8);
    }

    
    .github-icon {
        border-radius: 10px;
    }

    .github-icon:hover{
        width: 60px; /* Ajusta el tamaño del ícono de GitHub */
        height: 60px;
    }
}
